<template>
  <div id="notify-detail" class="find-box">
    <navigation-bar  @onLeftClick="goBack" pageName="消息详情" />
    <div class="refreshWrapper" v-if="detail" @click="replyInputShow = false">
      <!-- 加载的内容-->
        <section class="friend-message" v-if="themeCurrent == '0'">
          <div class="friend-msg">
            <div>
              <img v-if="detail.headPic" :src="detail.headPic" alt class="head"/>
              <img v-else src="../../assets/images/find/photo.png" alt class="head"/>
              <p>{{detail.nickName}}</p>
            </div>
          </div>
          <div class="message-container">
            <p>{{detail.content}}</p>
            <van-row
              class="message-img"
              v-bind:class="{'big-image':detail.notePic.length == 1}"
              v-if="detail.notePic"
            >
              <van-col span="6" v-for="(imgurl,index) in detail.notePic" v-bind:key="index">
                <img @click="preview(detail.notePic,index)" alt v-lazy="imgurl" :key="imgurl"/>
              </van-col>
            </van-row>
          </div>
          <div class="handler-container">
            <van-row span="8" type="flex" justify="space-between">
              <van-col class="desc">
                <span>{{detail.createdTime}}</span>
                <!-- <span @click="deleteNote(detail.noteCode)">删除</span> -->
              </van-col>
              <van-col span="8" class="handle-type" @click.stop>
                <span @click="setLike(detail.noteCode,detail.likerList)" style="padding-left: 15px">
                  <!-- <van-icon
                    v-if="checkLike(detail.likerList)"
                    name="like-o"
                    color="rgb(65,159,249)"
                    size="15px"
                  />
                  <van-icon v-else name="like-o" size="15px" /> -->
                  <img src="../../assets/images/find/unlove_icon.png" alt="" v-if="checkLike(detail.likerList)">
                  <img src="../../assets/images/find/love_icon.png" alt="" v-else>
                  <span v-if="detail.likerList">{{detail.likerList.length}}</span>
                </span>
                <span @click="udClick(null,detail.noteCode)">
                  <!-- <van-icon name="chat-o" size="15px" /> -->
                  <img src="../../assets/images/find/com_icon.png" alt="">
                  {{detail.commentList?detail.commentList.length:0}}
                </span>
              </van-col>
            </van-row>
          </div>
          <div class="reply-container" v-if="detail.likerList.length > 0 || detail.commentList.length > 0">
            <div class="goods-group" v-if="detail.likerList.length > 0">
              <img src="../../assets/images/find/praise_icon.png" alt="">
              <span v-for="(ele,a) in detail.likerList" v-bind:key="a">
                {{ele.nickName}}
                <i v-if="a != detail.likerList.length-1">,</i>
              </span>
            </div>
            <div v-if="detail.commentList.length > 0" class="reply-group-box">
              <div type="flex" class="reply-group" v-for="(eva,d) in detail.commentList" v-bind:key="d">
                <div class="reply-item" @click.stop>
                  <div class="reply-desc" @click="udClick(eva,detail.noteCode,0,false)">
                    <span class="reply-name">{{eva.nickName}}</span>
                    <img
                      v-if="eva.isProperty == 1"
                      src="../../assets/images/find/property.png"/>
                    <span class="result" v-if="eva.returner">回复</span>
                    <span
                      class="reply-name" v-if="eva.returner"
                    >{{eva.returner}}</span>
                    <img
                      v-if="eva.returnerType == 1"
                      src="../../assets/images/find/property.png"/>:
                    <span>{{eva.content}}</span>
                  </div>
                  <div class="eval-pic-box" v-show="eva.evalPic && eva.evalPic.length > 0">
                    <div v-for="(ind,c) in eva.evalPic" v-bind:key="c">
                      <img
                        @click="preview(eva.evalPic,c)"
                        v-lazy="ind"
                        :key="ind"
                        class="small-reply-img"
                        alt
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <van-row></van-row>
          </div>
      </section>
        <section class="friend-message-l" v-if="themeCurrent == '1'">
          <div class="friend-msg-l">
            <div>
              <img v-if="detail.headPic" :src="detail.headPic" alt class="head-l"/>
              <img v-else src="../../assets/images/find/photo.png" alt class="head-l"/>
              <p>{{detail.nickName}}</p>
            </div>
          </div>
          <div class="message-container-l">
            <p>{{detail.content}}</p>
            <van-row
              class="message-img-l"
              v-bind:class="{'big-image':detail.notePic.length == 1}"
              v-if="detail.notePic"
            >
              <van-col span="6" v-for="(imgurl,index) in detail.notePic" v-bind:key="index">
                <img @click="preview(detail.notePic,index)" alt v-lazy="imgurl" :key="imgurl"/>
              </van-col>
            </van-row>
          </div>
          <div class="handler-container-l">
            <van-row span="8" type="flex" justify="space-between">
              <van-col class="desc-l">
                <span>{{detail.createdTime}}</span>
                <!-- <span @click="deleteNote(detail.noteCode)">删除</span> -->
              </van-col>
              <van-col span="8" class="handle-type-l" @click.stop>
                <span @click="setLike(detail.noteCode,detail.likerList)" style="padding-left: 15px">
                  <!-- <van-icon
                    v-if="checkLike(detail.likerList)"
                    name="like-o"
                    color="rgb(65,159,249)"
                    size="15px"
                  />
                  <van-icon v-else name="like-o" size="15px" /> -->
                  <img src="../../assets/images/find/unlove_icon.png" alt="" v-if="checkLike(detail.likerList)">
                  <img src="../../assets/images/find/love_icon.png" alt="" v-else>
                  <span v-if="detail.likerList">{{detail.likerList.length}}</span>
                </span>
                <span @click="udClick(null,detail.noteCode)">
                  <!-- <van-icon name="chat-o" size="15px" /> -->
                  <img src="../../assets/images/find/com_icon.png" alt="">
                  {{detail.commentList?detail.commentList.length:0}}
                </span>
              </van-col>
            </van-row>
          </div>
          <div class="reply-container-l" v-if="detail.likerList.length > 0 || detail.commentList.length > 0">
            <div class="goods-group-l" v-if="detail.likerList.length > 0">
              <img src="../../assets/images/find/praise_icon.png" alt="">
              <span v-for="(ele,a) in detail.likerList" v-bind:key="a">
                {{ele.nickName}}
                <i v-if="a != detail.likerList.length-1">,</i>
              </span>
            </div>
            <div v-if="detail.commentList.length > 0" class="reply-group-box-l">
              <div type="flex" class="reply-group-l" v-for="(eva,d) in detail.commentList" v-bind:key="d">
                <div class="reply-item-l" @click.stop>
                  <div class="reply-desc-l" @click="udClick(eva,detail.noteCode,0,false)">
                    <span class="reply-name-l">{{eva.nickName}}</span>
                    <img
                      v-if="eva.isProperty == 1"
                      src="../../assets/images/find/property.png"/>
                    <span class="result-l" v-if="eva.returner">回复</span>
                    <span
                      class="reply-name-l" v-if="eva.returner"
                    >{{eva.returner}}</span>
                    <img
                      v-if="eva.returnerType == 1"
                      src="../../assets/images/find/property.png"/>:
                    <span>{{eva.content}}</span>
                  </div>
                  <div class="eval-pic-box-l" v-show="eva.evalPic && eva.evalPic.length > 0">
                    <div v-for="(ind,c) in eva.evalPic" v-bind:key="c">
                      <img
                        @click="preview(eva.evalPic,c)"
                        v-lazy="ind"
                        :key="ind"
                        class="small-reply-img-l"
                        alt
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <van-row></van-row>
          </div>
      </section>
    </div>
    <div class="reply-wrapper" v-show="replyInputShow" ref="myReply">
      <div class="box">
        <input type="text" :placeholder="commitType == 'reply' && replys ? '回复 ' + replys.nickName + ':' : '写下你此刻的想法'" autofocus ref="myInput" v-model="replyValue" maxlength="200">
        <van-button
          slot="button"
          size="small"
          type="primary"
          @click="submitInput"
          color="#fb5768"
        >发送</van-button>
      </div>
    </div>
      <van-action-sheet
        v-model="deleteShow"
        :actions="deleteActions"
        cancel-text="取消"
        round
        close-on-click-action
        safe-area-inset-bottom
        @select="delCommentAction"
        :style="{ color: '#FB5768' }"
      />
      <Preview :images="images" ref="myPreview" @indexChange="indexChange"></Preview>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { ImagePreview, Toast, Dialog } from "vant";
import { store } from "../../utils/storage";
import Preview from "@/components/preview/Preview.vue";
import typeJudge from "../../utils/typeJudge";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "FindNotifyDetail",
  created() {},
  mounted() {
    this.themeCurrent = this.$curTheme;
    this.noteCode = this.$route.params.noteCode;
    window.addEventListener("native.keyboardshow", this.keyboardshow);
    window.addEventListener("native.keyboardhide",this.keyboardhide);
    this.watcherScroll();
    this.getDetail();
  },
  destroyed() {
    window.removeEventListener("native.keyboardshow", this.keyboardshow);
    window.removeEventListener("native.keyboardhide", this.keyboardhide);
  },
  props: {},
  watch: {
    //每次变化，input获取焦点
    replyInputShow(val, oldVal) {
      if (val == true) {
        this.$nextTick(function() {
          this.$refs.myInput.focus();
        });
      } else {
        this.$nextTick(function() {
          this.$refs.myInput.blur();
        });
      }
    }
  },
  data() {
    return {
      deleteActions: [{ name: "删除" }],
      deleteShow: false,
      noteTotal: 0,
      background: {
        backgroundImage:
          "url(" + require("../../assets/images/find/find_bg.png") + ") ",
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover"
      },
      commitType: "",
      replys: null,
      ownerInfo: "",
      detail: null,
      replyInputShow: false,
      replyValue: "",
      noteCode: "",
      comitNoteCode: "",
      // 图片预览属性
      images: [],
      themeCurrent: 0,
    };
  },
  components: {
    Preview: Preview,
    NavigationBar
  },
  methods: {
    ...mapActions([
      "getNotesAction",
      "saveLikeAction",
      "getOneNoteMsgAction",
      "delNoteDataAction",
      "delCommentInforAction",
      "addCommentAction",
      "saveReplyAction",
      "getMineMessageCountAction",
      "getNoteDetailAction"
    ]),

    /**
     * 键盘弹出事件
     */
    keyboardshow(e) {
      // 安卓的处理不正确，有问题
      this.$refs.myReply.style.bottom = e.keyboardHeight + "px";
      this.replyInputShow = true;
    },

    /**
     * 键盘隐藏事件
     */
    keyboardhide(e) {
      this.replyInputShow = false;
      this.$refs.myReply.style.bottom = "0";
    },

    /**
     * 页面滚动检测，发生滚动后隐藏输入框
     **/
    watcherScroll() {
      document.querySelector(".find-box").addEventListener(
        "touchmove",
        e => {
          this.replyInputShow = false;
        },
        { passive: false }
      );
    },

    getDetail() {
      this.getNoteDetailAction({
        noteCode: this.noteCode
      }).then(
        result => {
          if (result.resultCode == "0") {
            this.detail = result.data;
          } else {
            this.$toast(res.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 评论或回复点击行为
     * @param
     * evaMap          Object<any>     当前回复对象数据
     * noteCode        Object<any>     当前消息对象数据
     * noteIndex?      Number          当前note索引
     * commentType?    Boolean         当前选择的回复类型
     **/
    udClick(evaMap, noteCode, noteIndex, commentType = true) {
      this.comitNoteCode = noteCode;
      if (commentType) {
        this.commitType = "comment";
        this.replyInputShow = true;
      } else {
        this.commitType = "reply";
        console.log(evaMap);
        if (this.$userInfo.OWNER_CODE === evaMap.userCode) {
          this.deleteShow = true;
          this.replyNoteIndex = noteIndex;
          this.delActionObj = {
            noteCode: noteCode,
            evalCode: evaMap.evalCode
          };
        } else {
          this.replyInputShow = true;
          this.replys = {
            nickName: evaMap.nickName,
            evalCode: evaMap.evalCode
          };
        }
      }
    },

    /**
     * 输入框内容提交
     *
     **/
    submitInput() {
      if(this.replyValue.length == 0) {
        return;
      }
      if (this.commitType == "reply") {
        this.replyAction();
      } else {
        this.addComments();
      }
      this.replyInputShow = false;
    },

    /**
     * 增加评论
     * @param
     * noteCode   String   当前选中note编码
     **/
    addComments() {
      this.addCommentAction({
        content: this.replyValue,
        isProperty: 0,
        noteCode: this.comitNoteCode,
        userCode: this.$userInfo.OWNER_CODE
      })
        .then(() => {
          this.$toast("评论成功");
          this.replyValue = "";
          this.getDetail();
        })
        .catch(error => {
          this.$toast(error);
        });
    },

    /**
     * 回复好友评论
     * @param
     * ownerCode   String      当前选择的评论用户编码
     **/
    replyAction(evaMap, noteMap) {
      let reqData = {
        content: this.replyValue,
        evalCode: this.replys.evalCode,
        isProperty: 0,
        userCode: this.$userInfo.OWNER_CODE
      };
      this.saveReplyAction(reqData)
        .then(() => {
          this.replyValue = "";
          this.getDetail();
        })
        .catch(error => {
          this.$toast(error);
        });
    },

    /**
     * 图片预览
     * @param
     * src    Array    图片url集合
     * index  Number   当前选中图片地址
     **/
    preview(src, index) {
      this.images = src;
      this.indexChange(index);
      this.$refs.myPreview.setIndex(index);
      this.$refs.myPreview.setIsShowPreview(true);
      this.$store.dispatch("setPreviewId", this.$refs.myPreview);
    },
    
    /**
     * index切换
     **/
    indexChange(index) {
      var src = this.images[index];
      if(src.lastIndexOf("@") == -1) {
        return;
      }
      var index1 = src.lastIndexOf(".");
      var index2 = src.lastIndexOf("@");
      var newSrc = src.substring(0, index2) + src.substring(index1, src.length);
      var img = new Image();
      img.onload = () => {
        this.$set(this.images, index, newSrc);
      }
      img.src = newSrc;
    },

    /**
     * 点赞行为(增加与取消)
     * @param
     * noteCode  String   当前点选节点code
     * likeMap   Array<Object> 当前节点所有点赞集合
     **/

    setLike(noteCode, likeMap) {
      let reqData = {
        concemflag: 1,
        isProperty: 0,
        noteCode: noteCode,
        userCode: this.$userInfo.OWNER_CODE
      };
      if (likeMap) {
        reqData.concemflag = likeMap.some(
          val => val.userCode == reqData.userCode
        )
          ? 0
          : 1;
      } else {
        reqData.concemflag = 1;
      }
      this.saveLikeAction(reqData)
        .then(res => {
          this.getDetail();
        })
        .catch(error => {
          this.$toast(error);
        });
    },

    /**
     * 删除评论操作
     * @param
     **/
    delCommentAction() {
      Dialog.confirm({
        title: "提示",
        message: "确定删除此条评论？"
      })
        .then(() => {
          let { noteCode, evalCode } = this.delActionObj;
          this.delCommentInforAction({ noteCode, evalCode })
            .then(() => {
              this.getDetail();
            })
            .catch(error => {
              console.log(error);
              this.$toast(error);
            });
        })
        .catch(() => {});
    },

    /**
     * 删除当前所选的note
     * @param
     *  noteCode  String  当前所选的noteCode
     **/
    // deleteNote(noteCode) {
    //   Dialog.confirm({
    //     title: "提示",
    //     message: "确定删除此贴？"
    //   }).then(() => {
    //     return delNoteDataService(noteCode)
    //       .then(result => {
    //         this.$router.go(-2);
    //       })
    //       .catch(error => {
    //         this.$toast(error);
    //       });
    //   });
    // },

    /**
     * 校验当前的点赞列表中是否包含当前登录用户，以此来更改点赞图标
     * @param
     *  likeMap  Array<Object>  当前的点赞集合数据
     * @return
     *  Boolean  当前登录账户是否已经点赞过
     **/
    checkLike(likeMap) {
      if (!likeMap) {
        return false;
      }
      console.log(likeMap);
      return likeMap.some(val => val.userCode == this.$userInfo.OWNER_CODE);
    },

    goBack() {
      if (this.$store.state.preview.previewId) {
        this.$store.dispatch("setPreviewId", "");
      }
      this.$router.go(-1);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped lang="scss">
$bigSize: 18px;
$smallSize: 14px;
$cSize: 16px;
$superSmall: 12px;
$replyColor: #5e9be3;
.van-nav-bar {
  height: 46px !important;
  position: relative !important;
}
#notify-detail {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  .refreshWrapper {
    flex: 1;
    overflow: scroll;
    .no-list {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      font-size: 14px;
      padding-top: 50px;
      img {
        width: 60px;
        margin-bottom: 15px;
      }
    }
  }
  .friend-message {
    padding: 13px 15px 0 13px;
    margin-bottom: 15px;
    .friend-msg {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > div:first-child {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        > .head {
          width: 38px;
          height: 38px;
          border-radius: 29px;
          object-fit: cover;
        }
        > p {
          margin: 0;
          margin-left: 12px;
          margin-top: 4px;
          text-align: left;
          font-size: $smallSize;
          max-width: 240px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .property {
          margin-top: 4px;
          margin-left: 5px;
          width: 40px;
          height: 18px;
        }
      }
    }
    .message-container {
      font-size: $cSize;
      text-align: left;
      line-height: 24px;
      word-wrap: break-word;
      font-family: PingFangSC-Regular;
      color: #333333;
      letter-spacing: -0.05px;
      > p {
        margin: 0;
        padding: 15px 0 10px 0;
      }
    }
    .message-img {
      /deep/.van-col {
        height: 84px;
      }
      img {
        width: 84px;
        height: 84px;
        object-fit: cover;
      }
    }
    .big-image {
      /deep/.van-col {
        height: 130px;
      }
      img {
        width: 130px;
        height: 130px;
      }
    }
    .handler-container {
      font-size: $superSmall;
      height: 36px;
      line-height: 36px;
      .desc {
        color: #9a9a9a;
        > span:first-child {
          margin-right: 14px;
        }
      }
      .handle-type {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #333333;
        img {
          width: 16px;
          height: 14px;
        }
        > span {
          display: flex;
          align-items: center;
          > img {
            margin-right: 5px;
          }
        }
        > span:last-child {
          padding-left: 16px;
        }
      }
    }
    .reply-container {
      padding: 3px 12px 7px 12px;
      background: #f7f7f7;
      .goods-group {
        min-height: 20px;
        line-height: 20px;
        word-break: break-all;
        vertical-align: middle;
        img {
          margin-right: 4px;
          width: 14px;
          height: 12px;
          vertical-align: middle;
        }
        span {
          vertical-align: middle;
          font-size: $smallSize;
          color: $replyColor;
          font-family: PingFangSC-Regular;
          margin-right: 2px;
          i {
            color: #151515;
          }
        }
      }
      .reply-group-box {
        margin-top: 10px;
        .reply-group {
          .reply-item {
            font-size: $smallSize;
            width: 100%;
            line-height: 23px;
            margin-bottom: 1px;
            .reply-desc {
              word-break: break-all;
              vertical-align: middle;
              span {
                vertical-align: middle;
              }
              > span.reply-name {
                color: $replyColor;
              }
              img {
                margin: 0 2px;
                width: 40px;
                height: 18px;
                vertical-align: middle;
              }
              > span.result {
                margin: 0 3px;
              }
            }
            .eval-pic-box {
              width: 100%;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              height: 40px;
              margin-top: 4px;
              div {
                width: 40px;
                height: 40px;
                margin-right: 5px;
              }
              .small-reply-img{
                width: 40px;
                height: 40px;
              }
            }
          }
        }
      }
    }
  }
  .friend-message-l {
    padding: 13px 15px 0 13px;
    margin-bottom: 15px;
    .friend-msg-l {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > div:first-child {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        > .head-l {
          width: 38px;
          height: 38px;
          border-radius: 29px;
          object-fit: cover;
        }
        > p {
          margin: 0;
          margin-left: 12px;
          margin-top: 4px;
          text-align: left;
          font-size: 18px;
          max-width: 240px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .property-l {
          margin-top: 4px;
          margin-left: 5px;
          width: 40px;
          height: 18px;
        }
      }
